<template>
  <div>
    <router-link to="/">5to7</router-link>
    <ul>
      <li v-if="!isLoggedIn"><router-link to="/login">Login</router-link></li>
      <li v-if="isLoggedIn" v-on:click="logoutUser()">
        Logout
      </li>
    </ul>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex';
    export default {
        name: "NavBarDash",
        computed: {
            ...mapGetters(['isLoggedIn'])
        },
        methods: {
            ...mapActions(['logout']),
            logoutUser() {
              this.logout();
            }
        }
    }
</script>

<style scoped>
li {
  cursor: pointer;
}
</style>
