<template>
  <div>
    <Nav/>
    <h1>Dashboard</h1>
    <ul class="list-group">
      <li class="list-group-item">Email: {{userInfo.email}}</li>
      <li class="list-group-item">Username: {{userInfo.username}}</li>
      <li class="list-group-item">Name: {{userInfo.name}}</li>
    </ul>
  </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex';
    import Nav from './NavBarDash';
    export default {
        name: "Dashboard",
        data() {
            return {
                userInfo: {
                    email: '',
                    username: '',
                    name: ''
                }
            }
        },
        components: {
          Nav
        },
        computed: {
            ...mapGetters(['user']),
        },
        methods: {
            ...mapActions(['getProfile'])
        },
        async mounted() {
            await this.getProfile();
            this.userInfo.email = this.user.email;
            this.userInfo.name = this.user.name;
            this.userInfo.username = this.user.username;


        }
    }
</script>

